.input {
  display: flex;
  padding: 5px;
  background: rgb(242, 191, 251);
  font-size: 18px;
  border-radius: 5px;
  margin: 5px;

  &__content {
    margin-right: 5px;
    font-weight: bold;
  }
  &__field {
    width: 5em;
    text-align: center;
  }
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}
